/** Blocks*/
.slider {
  margin-bottom: 40px;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.slider__container {
  width: 100%;
  padding-top: 38%;
  position: relative;
}
.slider__link {
  display: block;
  width: 100%;
  padding-top: 38%;
  position: relative;
}
.slider__image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.slider .carousel .slide {
  background: none;
}
.ReactModal__Body--open {
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
}

.light-box {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.light-box__container {
  display: inline-block;
}
.light-box__image {
  max-height: calc(100vh - 200px);
  width: inherit !important;
}
.light-box__close-btn {
  display: block;
  font-size: 2em;
  color: white;
  padding-bottom: 5px;
  outline: none;
  cursor: pointer;
  margin: 0 0 0 auto;
}
.light-box .carousel.carousel-slider {
  margin: auto 0;
}
.light-box .carousel .thumbs-wrapper {
  margin: 0;
}
.light-box .carousel .thumbs {
  margin: 16px 0 0;
  text-align: center;
}
.light-box .carousel .thumb {
  height: 50px;
  border: none;
  cursor: pointer;
}
.light-box .carousel .thumb .thumbnail__image {
  background-position: center;
  background-size: cover;
  border-radius: 2px;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.2);
  cursor: pointer;
  display: inline-block;
  height: 100%;
  margin: 2px;
  overflow: hidden;
  width: 100%;
}
.light-box .carousel .slider {
  align-items: center;
}
.light-box .carousel.carousel-slider .control-arrow {
  top: 50%;
  bottom: auto;
  transform: translateY(-50%);
  background: none;
  opacity: 1;
}
.light-box .carousel.carousel-slider .control-arrow:hover {
  background: none;
}

.carousel.carousel-slider {
  overflow: visible;
}
.carousel.carousel-slider .control-arrow {
  position: absolute;
  z-index: 1;
}
.carousel .control-dots {
  bottom: 0px;
}
.carousel .control-dots .dot {
  width: 10px;
  height: 10px;
  background-color: #ccc;
}
.carousel .control-dots .dot:hover {
  background-color: #fff;
}
.carousel .control-dots .dot.selected {
  background-color: #fff;
}
.carousel .control-arrow {
  width: 45px;
}